import React, { useEffect, useState } from "react";



import { getStore, gen16CodeId, ObjectKeys} from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { AddAccount_Cmpt, AccountItem_Cmpt, GlobalAccount_Cmpt } from "..";
import { getall_subaccount, getall_account, search_account, get_balances_by_type, calc_sub_myr } from "../services/server_fetching";
import { useNavigate } from "react-router";



const SearchInput = SearchInput_Cmpt();
const AddAccount = AddAccount_Cmpt();



const AccountItem = AccountItem_Cmpt();


const GlobalAccount = GlobalAccount_Cmpt();




const AccountDashboard  = ({  }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [loading, set_loading] = useState(false);




	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId());
			update_accounts_summary();
			get_balances_by_type("global_accounts","global_account_params",cb_result);
			getall_subaccount(cb_result);
			
		}
	});

	const navigate = useNavigate();


	const openUpdateView =  () => {
		
	}


	



	
	const openAddView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<AddAccount />
		
		OpenModal(modal_data);
	}
	
	const openGlobalAccView =  () => {

		let modal_data = {};
		modal_data["zIndex"] = 450;
		modal_data["height"] = "80vh";
		// data['observeResize']=true;
		modal_data["props"] = { minHeight: "1vh", overlay: true };
		modal_data["content"] = 
			<GlobalAccount />
		
		OpenModal(modal_data);
	}




	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_account =  (v) => {
		set_loading(true)
		search_account(cb_result)
	}


	const handle_getall_account =  (v) => {
		getall_account(cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};

	
	const update_accounts_summary =  () => {
		get_balances_by_type("accounts_summary_by_months","accounts_summary_by_months", cb_result);
	}

	const handle_calc_sub_myr =  () => {
		calc_sub_myr(cb_result);
	}

	const openCalendarModalPicker =  () => {
	//	downloadCSVFromJson('export_accounts_list_'+ gen16CodeId()+'.csv',prepareDatatoCvs())
	}


	
	const openPmBG =  (v) => {
		navigate({
			pathname: "/parameterize-balance"
		});
		
	}


	return (

	<div className="">
		<div className="accounts_header">
			<h2>Cuentas</h2>
			<div className="_dsplFlx ">
				<p className="group_title addBtn purple" onClick={openAddView}>
					Agregar Cuenta
				</p>
				<p className="group_title addBtn purple" onClick={openGlobalAccView}>
					Cuentas Globales
				</p>
				<p className="group_title addBtn purple" onClick={openPmBG}>
					Parametrizar Balances
				</p>	
				<p className="group_title addBtn purple" onClick={handle_calc_sub_myr}>
					Calcular SubMayor de Cuentas
				</p>
			</div>
			<div className="_dsplFlx " style={{marginTop:14}} >
				<SearchInput
					placeholder={"Buscar ...."} 
					width={360}
					obs={set_obs}
					fetchData={handle_search_account}
					loading={loading}
					change={handleChange}
					callempty={handle_getall_account}
					keyFlds={"searchQry"}
				/>
			</div>
		</div>

		
			
			<div className="accounts_container scollVh">
				{getStore("account_list") && ObjectKeys(getStore("account_list"))?.map(cid=>{
					return (
						<AccountItem 
							data={getStore("account_list")[cid]} 
							elmId={cid} 
							key={cid}
							openUpdateView={openUpdateView}
						/>
					)
				})}
			</div>
		
		</div>
	);
};

export default AccountDashboard;
